import styled from '@emotion/styled'
import { Alert, Badge, Button, IconButton } from '@mui/material'
import DataGridDemo from 'components/DataGridDemo'
import { data } from 'variables/data'
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart'
import { useState } from 'react'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'
import Autocomplete from '@mui/material/Autocomplete'
import { useNavigate } from 'react-router-dom'
import AlerCostum from 'components/AlerCostum'

export default function Main() {
  const [values, setValues] = useState(data)
  const [selected, setSelected] = useState([])

  const [message, setMessage] = useState()


  const navigate = useNavigate()

  const hundle = (value) => {
    setValues(values.filter((e) => e != value))
    setSelected([value, ...selected])
    setMessage(`${value.Product}`)
  }
  return (
    <div style={{ padding: 10 }}>
      <div style={{ display: `flex`, justifyContent: `flex-end` }}>
        <AlerCostum message={message} />
        <IconButton
          onClick={() => {
            navigate(`/ShoppingCart`, { state: { selected } })
          }}
        >
          <StyledBadge badgeContent={selected.length} color="secondary">
            <ShoppingCartIcon />
          </StyledBadge>
        </IconButton>
      </div>

      <div>
        <DataGridDemo data={values} hundle={hundle} />
        <Autocomplete
          options={values}
          autoHighlight
          getOptionLabel={(option) => option.Product}
          renderOption={(props, option) => (
            <Box
              component="li"
              sx={{ '& > img': { mr: 2, flexShrink: 0 } }}
              {...props}
            >
              {option.Product}
            </Box>
          )}
          renderInput={(params) => (
            <TextField
              {...params}
              label="Choose a country"
              inputProps={{
                ...params.inputProps,
                autoComplete: 'new-password', // disable autocomplete and autofill
              }}
            />
          )}
          onChange={(event, newValue) => {
            hundle(newValue)
          }}
          selectOnFocus
        />
      </div>
    </div>
  )
}
const StyledBadge = styled(Badge)(({ theme }) => ({
  '& .MuiBadge-badge': {
    right: -3,
    top: 13,
    border: `2px solid ${theme.palette.background.paper}`,
    padding: '0 4px',
  },
}))
