import { IconButton } from '@mui/material'
import DataGridDemo from 'components/DataGridDemo'
import React, { useEffect, useState } from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import ArrowBackIcon from '@mui/icons-material/ArrowBack'
import { ObjGroupBy } from 'utils/utils'

export default function ShoppingCart(props) {
  const { state } = useLocation()
  const navigate = useNavigate()
  const [selected, setSelected] = useState([])

  useEffect(() => {
    let arr = state.selected ?? []
    console.log(`🚩 . arr`, arr)

    arr = arr.map((e) => {
      const clone = { ...e }
      delete clone.Product
      console.log(`🚩 . clone`, clone)
      const min = Math.min(...Object.values(clone).filter((e) => e))
      console.log(`🚩 . min`, min)
      const cheapest = Object.entries(clone).find(([k, v]) => v === min)[0]
      return { cheapest, ...e }
    })

    setSelected(ObjGroupBy(arr, `cheapest`))
  }, [state])

  return (
    <div>
      <IconButton onClick={() => navigate(-1)}>
        <ArrowBackIcon />
      </IconButton>
      {Object.entries(selected).map((e, i) => {
        const [key, value] = e
        console.log(`🚩 . [key, value]`, [key, value])
        return (
          <div key={i}>
            <h4>{key}</h4>
            <DataGridDemo data={value} />
          </div>
        )
      })}
    </div>
  )
}
